import React, { Component, Fragment } from "react";
import ReactDOM from "react-dom";
import { withGlobalValue } from "../Context";
import styled from "styled-components";
import ClickOutside from "./ClickOutside";

const DivSearchTitle = styled.a`
  float: left;
  padding: 15px;
  &:link,
  &:visited {
    color: white;
    text-decoration: none;
  }
  &:hover {
    text-decoration: none;
    color: #555;
    background-color: #eee;
  }
  &:active {
    color: white;
    text-decoration: none;
  }
  @media (max-width: 640px) {
    display: none;
  }
`;

const MobileSearchTitle = styled.a`
  &:link,
  &:visited {
    color: white;
    text-decoration: none;
  }
  &:hover {
    text-decoration: none;
    color: #555;
    background-color: #eee;
  }
  &:active {
    color: white;
    text-decoration: none;
  }
  & > i {
    padding: 15px 20px;
  }
`;

const MobileBarMenuButton = styled.div`
  display: none;
  color: white;
  float: right;
  @media (max-width: 640px) {
    display: block;
    &:hover {
      color: #555;
      background-color: #eee;
      cursor: pointer;
    }
    background-color: ${props => (props.barMenuClicked ? "#eee" : null)};
  }
  & > i {
    padding: 10px 20px;
  }
`;

const DivContainer = styled.div`
  position: fixed;
  width: 100%;
  overflow: hidden;
  background-color: ${props => (props.theme ? props.theme : null)};
  z-index: 20;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
`;

const DivDropdownTab = styled.div`
  float: right;
  @media (max-width: 640px) {
    display: none;
  }
`;

const DivDropdownTitle = styled.div`
  float: right;
  display: block;
  padding: 15px;
  color: white;
  &:hover {
    color: #555;
    background-color: #eee;
    cursor: pointer;
  }
  background-color: ${props => (props.clicked ? props.theme : null)};
`;

const DivDropdownContainer = styled.div`
  position: fixed;
  top: ${props => props.x + props.h + "px"};
  left: ${props => props.y + "px"};
  background-color: white;
  z-index: 20;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
`;

const DivAccountContainer = styled.div`
  position: fixed;
  top: ${props => props.x + props.h + "px"};
  right: 0;
  background-color: white;
  z-index: 20;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
`;

const DivDropdownLink = styled.a`
  &:link,
  &:visited,
  &:hover,
  &:active {
    color: black;
    text-decoration: none;
  }
`;

const DivDropdownItem = styled.div`
  padding: 15px;
  &:hover {
    background-color: #eee;
    cursor: pointer;
  }
  @media (max-width: 640px) {
    color: white;
    background-color: ${props => props.theme};
  }
`;

const MobileDivTitle = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: white;
  &:hover {
    color: #555;
    background-color: #eee;
    cursor: pointer;
  }
  background-color: ${props => (props.clicked ? props.theme : null)};
  border-top: 1px solid #eee;
  & > i {
    padding: 15px 20px;
  }
  @media (min-width: 641px) {
    display: none;
  }
`;

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menuAccount: false,
      menuKC: false,
      mobileMenu: false,
      mobileMenuAccount: false,
      mobileMenuKC: false,
      menuTop: 0,
      menuRight: 0,
      menuHeight: 0,
      menuAccountTop: 0,
      menuAccountRight: 0,
      menuAccountHeight: 0
    };
    this._hideMenu = this._hideMenu.bind(this);
    this._toggleDropdown = this._toggleDropdown.bind(this);
  }

  render() {
    const { globalState } = this.props;
    const {
      menuAccount,
      menuKC,
      mobileMenu,
      mobileMenuKC,
      mobileMenuAccount
    } = this.state;
    return (
      <Fragment>
        <DivContainer theme={globalState.theme.LIGHT}>
          <DivSearchTitle href="/rates/search/">
            <i className="fas fa-search" /> Search
          </DivSearchTitle>

          <MobileDivTitle>
            <MobileSearchTitle href="/rates/search/">
              <i className="fas fa-search" /> Search
            </MobileSearchTitle>
            <MobileBarMenuButton
              onClick={event => this._toggleDropdown(event, "mobileMenu")}
            >
              <i className="fas fa-bars" />
            </MobileBarMenuButton>
          </MobileDivTitle>

          {mobileMenu ? (
            <ClickOutside customFunc={this._hideMenu} nameMenu={"mobileMenu"}>
              <MobileDivTitle
                clicked={this.state.menuKC}
                onClick={event => this._toggleDropdown(event, "mobileMenuKC")}
                theme={globalState.theme.DARK}
              >
                <i className="fas fa-globe-asia"> Knowledge Center</i>
                {mobileMenuKC ? (
                  <i className="fas fa-minus" />
                ) : (
                  <i className="fas fa-plus" />
                )}
              </MobileDivTitle>

              {mobileMenuKC ? (
                <Fragment>
                  <DivDropdownLink href="/search/locationcode/">
                    <DivDropdownItem theme={globalState.theme.DARK}>
                      <i
                        className="fas fa-map-marker-alt"
                        style={{ marginLeft: 20 }}
                      />{" "}
                      Location Code 
                    </DivDropdownItem>
                  </DivDropdownLink>
                  <DivDropdownLink href="/rates/charts/">
                    <DivDropdownItem theme={globalState.theme.DARK}>
                      <i
                        className="fas fa-chart-bar"
                        style={{ marginLeft: 20 }}
                      />{" "}
                       
                    </DivDropdownItem>
                  </DivDropdownLink>
                </Fragment>
              ) : null}

              <MobileDivTitle
                clicked={this.state.menuAccount}
                onClick={event =>
                  this._toggleDropdown(event, "mobileMenuAccount")
                }
                theme={globalState.theme.DARK}
              >
                <i className="fas fa-user-circle">
                  {" "}
                  {globalState.USER_PROFILE_NAME}
                  {" "}
                </i>
                {mobileMenuAccount ? (
                  <i className="fas fa-minus" />
                ) : (
                  <i className="fas fa-plus" />
                )}
              </MobileDivTitle>

              {mobileMenuAccount ? (
                <Fragment>
                  <DivDropdownLink href="/account/profile/">
                    <DivDropdownItem theme={globalState.theme.DARK}>
                      <i
                        className="fas fa-user-edit"
                        style={{ marginLeft: 20 }}
                      />{" "}
                       
                    </DivDropdownItem>
                  </DivDropdownLink>
                  <DivDropdownLink href="/account/friend/">
                    <DivDropdownItem theme={globalState.theme.DARK}>
                      <i
                        className="fas fa-user-plus"
                        style={{ marginLeft: 20 }}
                      />{" "}
                       
                    </DivDropdownItem>
                  </DivDropdownLink>
                  <DivDropdownLink href="/account/logout">
                    <DivDropdownItem theme={globalState.theme.DARK}>
                      <i
                        className="fas fa-power-off"
                        style={{ marginLeft: 20 }}
                      />{" "}
                      Log out
                    </DivDropdownItem>
                  </DivDropdownLink>
                </Fragment>
              ) : null}
            </ClickOutside>
          ) : null}

          <DivDropdownTab>
            <ClickOutside customFunc={this._hideMenu} nameMenu={"menuAccount"}>
              <DivDropdownTitle
                clicked={this.state.menuAccount}
                onClick={event => this._toggleDropdown(event, "menuAccount")}
                theme={globalState.theme.DARK}
              >
                <i className="fas fa-user-circle" />{" "}
                {globalState.USER_PROFILE_NAME}
              </DivDropdownTitle>
            </ClickOutside>
          </DivDropdownTab>
          <DivDropdownTab>
            <ClickOutside customFunc={this._hideMenu} nameMenu={"menuKC"}>
              <DivDropdownTitle
                clicked={this.state.menuKC}
                onClick={event => this._toggleDropdown(event, "menuKC")}
                theme={globalState.theme.DARK}
              >
                <i className="fas fa-globe-asia" /> Knowledge Center
              </DivDropdownTitle>
            </ClickOutside>
          </DivDropdownTab>
        </DivContainer>
        {menuAccount ? (
          <DivAccountContainer
            x={this.state.menuAccountTop}
            y={this.state.menuAccountRight}
            h={this.state.menuAccountHeight}
          >
            <DivDropdownLink href="/account/profile/">
              <DivDropdownItem>
                <i className="fas fa-user-edit" />  
              </DivDropdownItem>
            </DivDropdownLink>
            <DivDropdownLink href="/account/friend/">
              <DivDropdownItem>
                <i className="fas fa-user-plus" />  
              </DivDropdownItem>
            </DivDropdownLink>
            <DivDropdownLink href="/account/logout">
              <DivDropdownItem>
                <i className="fas fa-power-off" /> Log out
              </DivDropdownItem>
            </DivDropdownLink>
          </DivAccountContainer>
        ) : null}
        {menuKC ? (
          <DivDropdownContainer
            x={this.state.menuTop}
            y={this.state.menuRight}
            h={this.state.menuHeight}
          >
            <DivDropdownLink href="/search/locationcode/">
              <DivDropdownItem>
                <i className="fas fa-map-marker-alt" /> Location Code 
              </DivDropdownItem>
            </DivDropdownLink>
            <DivDropdownLink href="/rates/charts/">
              <DivDropdownItem>
                <i className="fas fa-chart-bar" />  
              </DivDropdownItem>
            </DivDropdownLink>
          </DivDropdownContainer>
        ) : null}
      </Fragment>
    );
  }
  _hideMenu = nameMenu => {
    this.setState({
      [nameMenu]: false
    });
  };
  _toggleDropdown = (event, data) => {
    let el = ReactDOM.findDOMNode(event.target).getBoundingClientRect();
    switch (data) {
      case "menuAccount":
        this.setState({
          [data]: !this.state.menuAccount,
          menuAccountTop: el.top,
          menuAccountRight: el.x,
          menuAccountHeight: el.height
        });
        break;
      case "menuKC":
        this.setState({
          [data]: !this.state.menuKC,
          menuTop: el.top,
          menuRight: el.x,
          menuHeight: el.height
        });
        break;
      case "mobileMenu":
        this.setState({
          [data]: !this.state.mobileMenu
        });
        break;
      case "mobileMenuAccount":
        this.setState({
          [data]: !this.state.mobileMenuAccount
        });
        break;
      case "mobileMenuKC":
        this.setState({
          [data]: !this.state.mobileMenuKC
        });
        break;
      default:
        break;
    }
  };
}

export default withGlobalValue(Header);



